import React from 'react';
import node from '../../assets/img/nodejs.png'
import mongo from '../../assets/img/mongods.png'
import Schema from '../../layout/schemas/ProgramsSingleSchema';
import Page from '../../layout/Page';
import Aux from '../../components/Aux';
import Popup from '../../components/Popup';

class Start extends React.Component {
  state = {
    showPopup: false,
    showSchema: false,
    seen: false,
    send: false,
    type: 'node'
  };

  componentDidMount() {
    const submitTime = localStorage.getItem(this.state.type);
    if (submitTime && parseInt(Date.now()) < (parseInt(submitTime))) {
      console.log(parseInt(Date.now()), (parseInt(submitTime)));
      this.setState({send:true, seen:true})
    }
    document.addEventListener('keydown', this.hidePopup);
    this.setState({showSchema: true});
    // setTimeout(() => {
    //   if (!this.state.seen) this.setState({showPopup: true})
    // }, 3000)
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.hidePopup)
  }

  hidePopup = (e) => {
    if (e === 'success') {
      this.setState({showPopup: false, seen: true, send: true})
    } else if (e.keyCode === 27 || e.type === 'click' || e === 'success') {
      this.setState({showPopup: false, seen: true})
    }
  };
  showPopup = () => {
    this.setState({showPopup: true, seen: true})
  };

  render() {

    return (
        <Aux>
          <Popup
              heading="Node.js Kursas"
              text="Registruokis ar gauk daugiau informacijos"
              type={this.state.type}
              show={this.state.showPopup}
              hidePopup={this.hidePopup}
          />
        <Page id="node" className="Program Node" description="Node.js kursas" title="[node.js]">

          {this.state.showSchema && <Schema/>}
          <div className="container">
            <h1>Serveri ir duomen bazi <span>konfiguravimas</span></h1>
            <br/>
            <h2>Apie program</h2>
            <p>
              is kursas specializuotas  serverins dalies programavim (back-end), duombazi prijungim, RESTful serverio krim, Linux operacini sistem konfigravim.
              <br/>
              Naudojamos technologijos: Node.js, MongoDB
            </p>
            <h2>
              Node.js
              <img src={node} className="logo-node" alt="node.js"/>
            </h2>
            <p>
              JavaScript kalba buvo sukurta internetinms naryklms. Dl savo populiarumo ir paprastumo ji labai ipopuliarjo, todl dar 2009 metais kompanija Google sukr Node.js rank, kuris leidia rayti JavaScript kalba u narykls rib.
              <br/>
              Node.js yra skirtas kurti "web" serverius, pasiymi didiuliu spartumu ir paprastumu. Programuotojams mokantiems JavaScript kalb, jis yra greitai perprantamas ir sisavinamas.
            </p>
            <h2>
              MongoDB
              <img src={mongo} className="logo-mongo" alt="mongoDB"/>
            </h2>
            <p>
              Tai NoSQL duombaze kuri yra ypa paplitusi tarp Node.js programuotoju. MongoDB ra struktra yra JSON formato tipo, lengvai prapleiama, paprasta konstruoti kompleksines uklausas naudojant JavaScript funkcijas.
            </p>
            <h2>Kursas</h2>
            <p>
              Kurso trukm 8 savaii (48val), po tris usimimus per savait po dvi valandas.
              <br/>
              Paskaitos vyksta kas antr dien, todl bus pakankamai laiko pasidaryti praktinius darbus ir imokti praeit mediag.
              <br/>
              Kursas prasideda nuo pai pagrind, isiaikinant kaip veikia serveris, kas yra API, asinchroninis veikimas. Kurso metu naudosime tik naujausi JavaScript ES6 sintaks. Sukonstruosime pilnai veikiant RESTful server, patalpinsime j i Linux operacin sistem.
              <br/>
              <b>Kurso eiga:</b>
            </p>
            <ul>
              <li>Isami ES6 JavaScript sintaks</li>
              <li>Node.js pagrindai, npm pakuoi sistema</li>
              <li>Express serveris</li>
              <li>"API endpoints" krimas</li>
              <li>MongoDB prijungimas</li>
              <li>Autentikacija ir saugumas</li>
              <li>Fail sistemos</li>
              <li>RESTful serverio krimas</li>
              <li>Serverio diegimas (deployment) Linux operacinje sistemoje</li>
              <li>Konsultacijos ir ikilusi problem aptarimas</li>
              <li>Pasiruoimas darbo pokalbiui</li>
            </ul>
            <h2>Bonus</h2>
            <p>
              Studentams prajusiems React.js kurs papildomos dvi savaits, kuri metu sujungsime abu rankius sukurdami pilnai veikiani aplikacij
            </p>
            <h3 className="offset">
              Trukm: 8 savaii - 48val
            </h3>
            <h3><span className="underline">Kaina: 195/mn</span></h3>
            <p className="offset">
              {/*<i>Artimiausias kursas: <b>2018-09-24</b></i>*/}

              {!this.state.send &&
              <button
                  onClick={this.showPopup}
                  className="register">
                Registracija              </button>
              }
            </p>
          </div>
        </Page>
        </Aux>
    );
  }
}
export default Start


